.cell {
  padding: 12px;
  box-sizing: border-box;
  border: 1px solid #182128;
  border-radius: 16px;
  margin: 6px auto;
  width: 100%;
  background-color: #313C44;
  position: relative;
  transition: 0.3s;

  &:hover {
    scale: 1.1;
  }

